    --opacity-color: rgba(240, 248, 255, 0.311);
}
html {
    background-color: black;
    overflow: scroll;
    background-size: cover;
    background-repeat: no-repeat;
    margin:0;
}
body {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap:15vh;
    border:solid red 1px;
}
h1 {
    color:white;
    position: fixed;
    background-position: 0 0;
    margin: 0;
    font-family: "Libre Franklin", sans-serif;
}
.week-container{
    display: inline-flex;
    justify-content: space-between;
    align-self: center;
    width:90vw;
    border:solid red 1px;
}
.day-div {
    display:table-cell;
    background-color: var(--opacity-color);
    border-radius: 5%;
    width:10vw;
    max-width: 200px;
    aspect-ratio: 1/1;
    color:white;
    font-size: calc(10vw/10);
    padding:3px;
    text-align: right;
    font-family: "Public Sans", sans-serif;
}
.today-div{
    margin-top:10vw;
    font-family: "Public Sans", sans-serif;
    background-color: var(--opacity-color);
    color:black;
    width:40vw;
    aspect-ratio: 1 / 1;
    max-width: 400px;
    align-self: center;
    font-size: calc(40vw/12);
    color:white;
    border-radius:5%;
    padding:10px;
}
.searchbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: var(--opacity-color);
    border-radius: 7px;
    border:none;
    width:15vw;
    height:20px;
    min-width: 110px;
    align-self: flex-end;
    margin-right: 10px;
    padding:5px;
    position: fixed;
    background-position: 85vw 0;
}
input[type="search"]{
    background-color: transparent;
    border: none;
    outline: none;
    width:12vw;
    min-width: 90px;
    height: 20px;
    color:white;
}
input[type="search"]::-webkit-input-placeholder{
    color:white;
}
button[type="submit"]{
    border:none;
    background-color: transparent;
    width:15px;
    height:15px;
    margin-right: 10px;
    background: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) no-repeat;
    background-size: contain;
}


#today {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}
p {
    font-size: 20px;
    width:90%;
}
.weather-container {
    display:inline-flex;
    justify-content: center;
    
}
.weather-type-img {
    width:100%;
    height:80%;
}
.weather-info {
    width:100%;
    height:80%;
}
p{
    display: flex;
    align-items: center;
    
}
